@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


html, body, #root, .App, .App>div, .game-container, .full-height {
  height: 100%;
  background-color: #82D2FE;
  font-family: 'Open Sans', sans-serif;
}

.App {
  text-align: center;
}



p {
  font-family: 'Open Sans', sans-serif;
}

div {
  margin: 0 auto;
  font-family: 'Open Sans', sans-serif;
}

.centered {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%) scale(2);
  color: white;
}

.row.option {
  margin-top: 15px;
}

.row.option:first-child {
  margin-top: 35px;
}

.option-button {
  margin: 0 auto;
  padding: .575rem .75rem;
}

.option-text {
  font-family: 'Open Sans', sans-serif;
  font-size: smaller;
}

.question {
  font-size: 20px;
  margin-top: 30px;
  color: white;
}
.separator::after,
.separator::before {
  content: "";
  flex: 1 1;
}

.black::after,
.black::before{
  border-top: 1px solid black;
}

/* .white::after,
.white::before {
  border-top: 1px solid white;
} */

.topic {
  display: flex;
  align-items: center;
  margin: 45px 0 15px;
}

.topic-name {
  margin-bottom: 0;
  padding: 1px 25px;
  font-weight: bold;
  font-size: .8rem;
}

.with-footer-container {
  min-height: 100%;
  position: relative;
}

.with-footer-content {
  background-color: #000;
  padding-bottom: 2rem;
  border-top: 2px #fff solid;
  margin-top: 2rem;
  min-height: 75vh!important;
  border-bottom: 2px #fff solid;
}
.footer {
  height: 3rem;
}
.icon {
  margin-right: 5px;
  margin-top: -3px;
}
span {
  font-family: 'Open Sans', sans-serif;
}

.final {
  color: white;
  font-size: 28px;
}

.off-total {
  color: white;
}
.game-container {
  padding-top: 25px;
  background-position: center;
}

.light {
  background-color: white;
}


.countdown-number {
  font-size: 30px;
  font-weight: 600;
}

.ready {
  font-size: 25px;
  font-weight: 600;
  margin-top: 10rem;
  color: white;
}
.result {
  margin-top: .5rem;
}

.score-container {
  color: white;
  font-size: 14px;
}

.score-description {
  margin-top: 3rem;
}

.score-secondary-description {
  margin-bottom: 0px;
  margin-top: 2em;
}

.score-more-info {
  margin-top: 0px;
}

.play-again {
  margin: 1rem 0 1rem;
}

.meme {
  margin: 1.5rem;
}

.more-info {
  margin-bottom: 0;
}

.merepresenta-button {
  margin: .5rem 0 3rem;
  border-radius: 30px!important;
}

.social-networks-logos>* {
  margin: .5rem 0 .5rem;
}

.twitter {
  margin-left: 3rem;
}

.score-footer {
  padding-bottom: 3rem;
}

.loading-logo {
  transform: scale(3);
}

.loading-background {
  animation-name: fade;
  animation-duration: 1s;
}


.share-text{
  font-size: 16px;
  font-weight: 700;
}
.iconos{
  margin-left: 1rem;
  margin-right: 1rem;
  cursor: pointer;
}
.call-to{
  margin-top: 1rem;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
}

.game-images {
  height: 30px;
}


@keyframes fade {
  0% {opacity: 0.9;}
  20% {opacity: 1;}
  33% {opacity: 1;}
  53% {opacity: 1;}
  100% {opacity: 1;}
}

.avergas svg g text  {
  fill: blue;
}
.game-name {
  font-family: 'Open Sans', sans-serif;
  font-size: 18px;
  margin-top: 1rem;
  /* font-weight: 800; */
}
.topics {
  color: white;
  font-size: 18px;
}

.play {
  font-size: 20px;
  font-weight: 600;
  background-color: #3A97C9;
  border: none;
}

.game-owner {
  font-size: 10px;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}
